export default {
	props: {
		songSheetData: {
			type: Array,
			default () {
				return []
			}
		}
	},
	filters: {
		filterNumbers(value) {
			if (value < 10000) return value;
			return (value / 10000).toFixed(0) + "ä¸‡";
		}
	},
	methods: {
		songSheetClick(item) {
			uni.navigateTo({
				url: '/pages/song-sheet/song-sheet?id=' + item.id
			})
		}
	}
}
